import { Component, EventEmitter, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { IMapKeyValue, IOptionsPsActionHyperlink, IOptionsPsContainerLookupOptionComponentExposed } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonSettings } from 'src/app/commonSRC/psServices/models/ps-common.settings';
import { PsTemplateListPage } from 'src/app/commonSRC/psTemplates/ps-template-list/ps-template-list.template';
import { CommonBussinessConstant } from '../../psServices/models/ps-common-bussiness-constant';
import { IListContainerLookupOption } from '../../psServices/models/ps-common-bussiness-interfaces';
import { PsCommonBusinessSettings } from '../../psServices/models/ps-commonBusiness.settings';
import { OmniPullService } from '../../psServices/omni-common/omni-pull.service';
import { IOmniScheduledTransfer, IOptionsPsOptionScheduledTransferExposed } from './ps-scheduled-transfers-list.component.interface';



@Component({
    selector: 'ps-scheduled-transfers-list',
    templateUrl: './ps-scheduled-transfers-list.component.html',
    styleUrls: ['./ps-scheduled-transfers-list.component.scss'],
})
export class PsScheduledTransfersListComponent extends PsTemplateListPage implements OnInit, OnDestroy {
    public updateList: any[];
    public bRefresh = false;
    @Input() options: IOptionsPsOptionScheduledTransferExposed;
    lookUpOptions: IListContainerLookupOption[] = [];
    public labelValuesMap = new Map<string, IMapKeyValue>();
    public headerMap = new Map<string, IMapKeyValue>();
    allowEdit: boolean = false;
    allowDelete: boolean = false;
    showDetails: boolean = false;

    @Output() reloadFct = new EventEmitter<string>();
    constructor(
        private omniPull?: OmniPullService) {
        super();
        this.populateHeaderValueMap();
    }
    ngOnInit() {
        super.init();
        if (this.options && this.options.listOfOptions) {
            this.options.listOfOptions.map((element, index) => element['rowIndex'] = index);
            this.options.itemList = this.options.listOfOptions;
        }

        this.allowEdit = this.allowDelete = PsCommonSettings.oper_ID === CommonBussinessConstant.SCHEDULED_TRANSFERS_OPER_ID
            || PsCommonSettings.oper_ID === CommonBussinessConstant.PENDING_TRANSFERS_OPER_ID


        this.showDetails = (PsCommonSettings.oper_ID === CommonBussinessConstant.SCHEDULED_TRANSFERS_OPER_ID);
    }

    prepareLookUp(card: IOmniScheduledTransfer): any {
        const lookupDetails: IOptionsPsContainerLookupOptionComponentExposed = {};
        lookupDetails.labelsValueMap = this.labelValuesMap;
        lookupDetails.headerMap = this.headerMap;
        lookupDetails.formGroup = this.options.group;
        lookupDetails.isEditable = this.options.isEditable;
        lookupDetails.itemList = this.options.itemList;
        lookupDetails.showInitialCardValues = 5;
        (PsCommonSettings.oper_ID != CommonBussinessConstant.PENDING_TRANSFERS_OPER_ID) ? lookupDetails.statementOptions = {
            buttonIcon: 'list-box',
            redirectUrl: 'scheduled-transfer-statement-report'
        } : {};
        lookupDetails.actionDetailsOptions = [

            ((this.allowEdit) ?
                {
                    id: 'edit',
                    label: 'edit_key',
                    actionHyperlink: {
                        group: this.options.group,
                        iconOptions: {
                            iconName: 'edit1',
                            labelOptions: {
                                labelKey: 'edit_key'
                            }
                        },
                        route: 'payment',
                        pageOptions: {
                            operId: card.transferTypeOperId,
                            iconName: 'edit1',
                            title: card.operName || 'pending_transfers_key' //modified by ghada, AZDB-5974 for opening each transfer screen with it's related oper name
                        },
                        preCallFunction: {
                            func() {
                                return new Promise<any>((resolve, reject) => {
                                    resolve(this.executionClass.prepareTransaction(lookupDetails.actionDetailsOptions[0].actionHyperlink));
                                });
                            },
                            executionClass: this,
                            params: []
                        },
                        navigationOptions: {
                            queryParams: { transferScheduledList: true },
                        },
                        psClass: 'pending-transfer-actions edit-pending'
                    }
                } : {}),
            ((this.showDetails) ? {
                id: 'details',
                label: 'details_key',
                actionHyperlink: {
                    group: this.options.group,
                    iconOptions: {
                        iconName: 'document',
                        labelOptions: {
                            labelKey: 'details_key'
                        }
                    },
                    route: 'payment',
                    pageOptions: {
                        operId: card.transferTypeOperId,
                        iconName: 'document',
                        title: 'pending_transfers_key'
                    },
                    navigationOptions: {
                        queryParams: { transferScheduledList: true, readOnlypage: true },
                    },
                    preCallFunction: {
                        func() {
                            return new Promise<any>((resolve, reject) => {
                                resolve(this.executionClass.prepareTransaction(lookupDetails.actionDetailsOptions[1].actionHyperlink));
                            });
                        },
                        executionClass: this,
                        params: []
                    },
                    psClass: 'pending-transfer-actions details-pending'
                }
            } : {}),
            ((card.transactionStatus === 'A' && this.allowDelete) ? {
                id: 'delete',
                labelOptions: {
                    labelKey: 'delete_key'
                },

                buttonIcon: 'delete',
                actionUrl: PsCommonSettings.serviceUrl.commonSubmitAction,
                cssClass: 'pending-transfer-actions delete-pending fab-edit-button',
                label: 'delete_key',
                iconName: 'trash',
                actionType: 'delete',
                operId: PsCommonSettings.oper_ID === CommonBussinessConstant.PENDING_TRANSFERS_OPER_ID ? CommonBussinessConstant.DELETE_IMMEDIATE_TRANSFER : CommonBussinessConstant.DELETE_STANDING_ORDER,

                postCallFunction: {
                    func() {
                        return new Promise<any>((resolve, reject) => {

                            resolve(this.executionClass.reloadList());

                        });
                    },
                    executionClass: this,
                    params: []
                },
                detailServiceUrl: 'rest/omniCorePull/returnTransactionDetails',
                detailServiceCallOnAction: PsCommonBusinessSettings.isCorporate ? true : false

            } : {}),
            ((this.allowStop(card.transactionStatus)) ?
                {
                    id: 'stop',
                    labelOptions: {
                        labelKey: 'stop_key'
                    },
                    buttonIcon: 'delete',
                    actionUrl: PsCommonSettings.serviceUrl.commonSubmitAction,
                    cssClass: 'pending-transfer-actions details-pending fab-edit-button',
                    label: 'stop_key',
                    iconName: 'square-outline',
                    actionType: 'Submit',
                    operId: PsCommonSettings.oper_ID === CommonBussinessConstant.CASH_TRANSFER_REP_OPER_ID ? CommonBussinessConstant.STOP_CASH_TRANSFER_OPER_ID : CommonBussinessConstant.STOP_STANDING_ORDER,
                    filterParamValues: ['transactionNumber'],
                    alertLabel: 'are_you_sure_you_want_to_stop_key',
                    alertType: 'prompt',
                    postCallFunction: {
                        func() {
                            return new Promise<any>((resolve, reject) => {
                                resolve(this.executionClass.reloadList());
                            });
                        },
                        executionClass: this,
                        params: []
                    },
                } : {})
        ];
        (PsCommonSettings.oper_ID != CommonBussinessConstant.PENDING_TRANSFERS_OPER_ID && PsCommonSettings.oper_ID != CommonBussinessConstant.CASH_TRANSFER_REP_OPER_ID) ? lookupDetails.pageData = {
            operID: ConstantCommon.VIEW_SCHEDULED_TRANSFERS_STATEMENT,
            title: 'scheduled_transfers_report_key'
        } : {};
        lookupDetails.reloadFunction = {
            func() {
                return new Promise<any>((resolve, reject) => {
                    resolve(this.executionClass.reloadList());
                });
            },
            executionClass: this,
            params: []
        }
        return lookupDetails;
    }

    reloadList() {
        this.reloadFct.emit();
    }

    private populateHeaderValueMap() {
        let typeMap: IMapKeyValue = {};
        let fromAccountMap: IMapKeyValue = {};
        let toAccountMap: IMapKeyValue = {};
        let amountMap: IMapKeyValue = {};
        let dateMap: IMapKeyValue = {};
        let referenceMap: IMapKeyValue = {};
        let statusMap: IMapKeyValue = {};
        let headerTitleMap: IMapKeyValue = {};
        let headerSubtitleMap: IMapKeyValue = {};
        let transactionNumberMap: IMapKeyValue = {};
        let bankCifShortNameEngMap: IMapKeyValue = {};
        let transactionDateMap: IMapKeyValue = {};
        let idTypeNumberMap: IMapKeyValue = {};
        let purposeMap: IMapKeyValue = {}

        transactionDateMap = {
            key: 'transactionDate',
            value: '--- --- --- ---',
            isEdit: false,
        };

        bankCifShortNameEngMap = {
            key: 'bankCifShortNameEng',
            value: '--- --- --- ---',
            isEdit: false,
            showMappingOnNoValue: true
        };

        transactionNumberMap = {
            key: 'transactionNumber',
            value: '--- --- --- ---',
            isEdit: false,
        };
        typeMap = {
            key: 'transferType',
            value: '--- --- --- ---',
            isEdit: false,
        };
        referenceMap = {
            key: 'reference',
            value: '--- --- --- ---',
            isEdit: false,
        };
        fromAccountMap = {
            key: 'formattedFromAccount',
            value: '--- --- --- ---',
            isEdit: false,
        };
        toAccountMap = {
            key: 'formattedToAccount',
            value: '--- --- --- ---',
            isEdit: false,
            showMappingOnNoValue: true

        };
        statusMap = {
            key: 'statusDescription',
            value: '--- --- --- ---',
            isEdit: false,
        };
        amountMap = {
            key: 'transactionAmountNCurrency',
            value: '--- --- --- ---',
            isEdit: false,
        };
        dateMap = {
            key: 'date',
            value: '--- --- --- ---',
            isEdit: false,
        };

        idTypeNumberMap = {
            key: 'idTypeNumber',
            value: '---/---',
            isEdit: false,
        }

        purposeMap = {
            key: 'purpose',
            value: '------',
            isEdit: false,
        }

        headerTitleMap = {
            key: 'title',
            value: 'title',
            isEdit: false,
            formGroupMap: {
                placeHolder: 'enter_transfer_type',
                fcName: 'transferTypeTitle',
            }
        };

        headerSubtitleMap = {
            key: 'subTitle',
            value: 'subTitle',
            isEdit: false,
            formGroupMap: {
                placeHolder: 'transfer_type',
                fcName: 'transferTypeSubtTitle',
            }
        };

        this.labelValuesMap.set('transaction_number_key', transactionNumberMap);
        this.labelValuesMap.set('from_account_key', fromAccountMap);
        this.labelValuesMap.set('amount_key', amountMap);
        this.labelValuesMap.set('trade_date_key', transactionDateMap);
        this.labelValuesMap.set('value_date_key', dateMap);

        if (PsCommonSettings.oper_ID !== CommonBussinessConstant.CASH_TRANSFER_REP_OPER_ID) {
            this.labelValuesMap.set('to_account_key', toAccountMap);
            this.labelValuesMap.set('type_key', typeMap);
            this.labelValuesMap.set('reference_key', referenceMap);
            this.labelValuesMap.set('bank_key', bankCifShortNameEngMap);
        } else {
            this.labelValuesMap.set('id_type_number_key', idTypeNumberMap);
            this.labelValuesMap.set('purpose_key', purposeMap);
        }
        this.labelValuesMap.set('status_key', statusMap);
        this.headerMap.set('title', headerTitleMap);
        this.headerMap.set('subTitle', headerSubtitleMap);
    }


    prepareTransaction(actionCard: IOptionsPsActionHyperlink): Promise<any> {
        return new Promise<any>((resolve, reject) => {
            const generalRequestData: any = {
                commonParametersList: {
                    transactionNumber: actionCard.navigationOptions.queryParams.transactionNumber,
                    transferTypeOperId: actionCard.navigationOptions.queryParams.transferTypeOperId, // By Mohanad AZDB-6915 - 26012025
                    transactionBranch: actionCard.navigationOptions.queryParams.transactionBranch || null,//Jira #5525 Send transactionBranch if have valid value
                }
            };
            this.omniPull.returnTransactionDetails(generalRequestData).then((result) => {
                result.data.operId = actionCard.navigationOptions.queryParams.transferTypeOperId; // By Mohanad AZDB-6915 - 26012025
                const data = this.commonProv.prepareResult(result.data);
                if (data.toBeneficiary && !data.toBeneficiary_lookupKey) {
                    data.toBeneficiary_lookupKey = data.toBeneficiary
                }
                //Jira #7124 - In case toAccount_lookupKey was missing , inject it manually to populate the TO Account component
                if (data.toAccount && !data.toAccount_lookupKey) {
                    data.toAccount_lookupKey = data.toAccount
                }
                this.commonProv.copyObject(actionCard.navigationOptions.queryParams, data, false);
                this.commonProv.copyObject(actionCard.navigationOptions.queryParams.ScreenVO, data, false);
                //===Jira #7235 In case commonResponseList exists, merge its objects with the root params to include possible dynamic Fields values
                if(typeof data.commonResponseList !== "undefined" ){
                    this.commonProv.copyObject(actionCard.navigationOptions.queryParams, data.commonResponseList, false);
                    this.commonProv.copyObject(actionCard.navigationOptions.queryParams.ScreenVO, data.commonResponseList, false);
                }
                actionCard.navigationOptions.queryParams = {
                    ScreenVO: { ...actionCard.navigationOptions.queryParams },
                    ...actionCard.navigationOptions.queryParams
                };

                resolve(null);
            });
        });
    }
    //edited by Marina-Jira-7926
    allowStop(statusCode) {
        //Makki: #6557 , As per Mirna Mezher, Stop should only be allowed for Created only in case of Sch Transfer
        //Makki: #7092 , As per Mirna Mezher, Stop should only be allowed for Approved only in case of Cash Transfer
        return (
            (statusCode === CommonBussinessConstant.TRANSFER_STATUS_CREATED && PsCommonSettings.oper_ID === CommonBussinessConstant.SCHEDULED_TRANSFERS_OPER_ID)
            ||
            (statusCode === CommonBussinessConstant.TRANSFER_STATUS_APPROVED && PsCommonSettings.oper_ID === CommonBussinessConstant.CASH_TRANSFER_REP_OPER_ID)
        );
    }

}
